html {
  font-family: "Times New Roman", serif;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: inherit;
  transition: inherit;
}

a {
  font: inherit;

  text-decoration: none;
  color: inherit;

  &:hover {
    opacity: 0.6;
  }

  &:active,
  &:focus {
    opacity: 0.3;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.fade {
  animation-name: fade;
  animation-duration: 0.45s;
}

.fade-out {
  animation-name: fade-out;
  animation-duration: 0.45s;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

