.controls {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-end;
}

.controls button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.8rem;
  margin: 0.3rem;
  cursor: pointer;
  transition: 0.2s;
  text-align: center;
  vertical-align: top;
  background: transparent;
  border-radius: 50%;
  appearance: none;
}

.controls__dot {
  display: flex;
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  background-color: #71abff;
  border: 0.3rem solid #71abff;
  border-radius: 50%;
}

.controls button.active .controls__dot {
  background-color: transparent;
}

.controls button:hover .controls__dot {
  background-color: #ffffff;
  border: 0.3rem solid #ffffff;
}

.controls button.active:hover .controls__dot {
  background-color: transparent;
  border: 0.3rem solid #ffffff;
}

.controls button:active .controls__dot {
  background-color: #bcbdbe;
  border: 0.3rem solid #bcbdbe;
}
