.slider {
  margin: 0 auto;
  padding: 10rem 2rem;
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  max-width: 94rem;
  width: 100%;
}

.slider__pictures {
  width: 100%;
  margin-bottom: 3rem;
}

.slider__img-wrapper {
  width: 100%;
  display: none;
}

.slider img {
  width: 100%;
  height: 91rem;
  object-fit: cover;
  border-radius: 2rem;

  @media (max-width: 93.9rem) {
    height: calc(100vw - 4rem);
  }
}

.slider__desc {
  font-size: 3rem;
  color: #ffffff;
  width: 60%;
  margin-top: 0.35rem;
}

.slider__desc-item {
  display: none;
}

.slider__controls {
  width: 25%;
}
